@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
    width: 100%;            
    height: 100vh;
    box-sizing: border-box;  
    display: flex;
    align-items: center;
    justify-content: center;
}

.wrapper {
    overflow: hidden;
    position: relative;
    width: 450px;
    max-width: 95%;
    margin: auto;
    box-shadow: 0 10px 10px rgba(0,0,0,0.25), 0 5px 10px rgba(0,0,0,0.22);
    border-radius: 10px;
    height: 95%;
    box-sizing: border-box;  
}


.search-btn {
    background: orange;
    outline: none;
    padding: 20px;
    font-size: 20px;
    color: white;
    border-radius: 100%;
    border: none;
    box-shadow: 1px 3px 5px rgba(0,0,0,0.4);
    position: absolute;
    bottom: 0;
    right: 0;    
}


/* HEADER */
.header-wrapper {
    border-radius: 0 0 40% 40%;
    width: 100%;
    height: 370px!important;
    overflow: hidden;
    position: relative;
}
.header-img {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
}
.header-content {
    padding: 20px 30px;
    color: white;
    text-shadow: 1px 1px 2px rgba(0,0,0,0.5);               
}
.header-content h1 {
    font-size: 90px;
    margin: 0px;
}
h1 sup {    
    font-size: 0.5em!important;
}

.location-text {
    font-size: 2em;
}

/* END HEADER */
